import { Footer } from "components/footer/footer";
import { Header } from "components/header";
import { PageProps } from "gatsby";
import { Helmet } from "gatsby-plugin-react-i18next/dist/Helmet";
import { PageRedirect } from "helpers/page-redirect/page-redirect";
import { useCurrentPage } from "navigation/use-current-page";
import * as React from "react";
import { Background } from "wrapper/background";
import { Suspense } from "wrapper/suspense";

type LayoutProps = {} & PageProps;

const Layout: React.FC<LayoutProps> = ({ children }) => {
  const page = useCurrentPage();

  if (page == null || page.disabled) {
    return <PageRedirect to={"main"} />;
  }

  return (
    <Suspense fallback={""}>
      <Background page={page.key}>
        <Helmet>
          <title>{page.title}</title>
          <meta name="description" content={page.description} />
        </Helmet>

        <Header />
        <main>{children}</main>
        <Footer />
      </Background>
    </Suspense>
  );
};

export default Layout;
