import * as React from "react";
import { Horizontal as HorizontalNav } from "./horizontal";
import { Vertical as VerticalNav } from "./vertical";

type LayoutProps = {
  type: "mobile" | "desktop";
};

export const Layout: React.FC<LayoutProps> = ({ type }) => {
  switch (type) {
    case "desktop":
      return <HorizontalNav />;
    case "mobile":
      return <VerticalNav />;
  }
};