import { RouteKeys } from "navigation/pages-list";
import * as React from "react";
import classNames from "classnames";

type BackgroundProps = {
  page?: RouteKeys;
};

export const Background: React.FC<BackgroundProps> = ({ page, children }) => {
  return (
    <div className={classNames("page-background", `page-background__${page}`)}>
      {children}
    </div>
  );
};
