import * as React from "react";
import { useIsBrowser } from "utils/is-browser";

export const Suspense: React.FC<React.SuspenseProps> = ({
  children,
  fallback,
}) => {
  const isBrowser = useIsBrowser();

  if (isBrowser) {
    return <React.Suspense fallback={fallback}>{children}</React.Suspense>;
  }

  return <>{children}</>;
};
