import { RouteKeys, routes } from "navigation/pages-list";
import * as React from "react";
import { Link as BaseLink } from "gatsby-plugin-react-i18next";
import { GatsbyLinkProps } from "gatsby";

type LinkProps = {
  to: RouteKeys;
} & Omit<GatsbyLinkProps<any>, "to"> & {
    language?: string;
  };

export const PageLink: React.FC<LinkProps> = ({ to, ...spread }) => {
  const link = routes[to];

  return <BaseLink to={link.path} {...spread} />;
};
