import { Header as BaseHeader } from "@greenpool-eco/components";
import * as React from "react";
import * as Navigation from "navigation/layout";
import { PageLink } from "helpers/page-link";
import { useCurrentPage } from "navigation/use-current-page";
import { RouteKeys } from "navigation/pages-list";
import { useIsResponsiveNav } from "utils/is-responsive-nav";
import { usePrevPage } from "utils/use-prev-page";
import "./styles.scss";

type HeaderProps = {};

export const HeaderNav: React.FC<HeaderProps> = () => {
  const [path, isNavigation] = usePath();
  const isResponsiveNav = useIsResponsiveNav();

  return (
    isResponsiveNav ? (
      <PageLink to={path}>
        <BaseHeader.MenuHamburger isOpen={isNavigation} />
      </PageLink>
    ) : <Navigation.Layout type="desktop" />
 );
};

function usePath(): [RouteKeys, boolean] {
  const prevPage = usePrevPage(["nav"]);
  const page = useCurrentPage();
  const isNavigation = page?.key === "nav";

  if (isNavigation) {
    return [prevPage?.key ?? "main", isNavigation];
  }

  return ["nav", isNavigation];
}
