import { navList } from "./nav-list";
import { Navigation } from "@greenpool-eco/components";
import { Link, useI18next } from "gatsby-plugin-react-i18next";
import * as React from "react";
// import { Actions } from "./actions";

export const Horizontal = () => {
  const { originalPath, t } = useI18next("navigation");

  return (
    <div className="right">
      <Navigation.Layout orientation={"horizontal"}>
        {navList.map((item) => {
          if (item.page.disabled) {
            return null;
          }

          return (
            <Navigation.Item
              isActive={originalPath.startsWith(item.page.path)}
              key={item.title}
            >
              <Link to={item.page.path}>{t(item.title)}</Link>
            </Navigation.Item>
          );
        })}
      </Navigation.Layout>
      {/*<Actions />*/}
    </div>
  );
};
