export type Page<Key extends string = string> = {
  key: Key;
  path: string;
  title: string;
  description: string;
  exact?: boolean;
  disabled?: boolean;
};

export type Routes<Key extends string = string> = {
  [key in Key]: Page<Key>;
};

export const createPages = <Keys extends string = string>(
  pages: Page<Keys>[]
) => {
  return {
    pages,
    keys: pages.map((page) => page.key),
    routes: pages.reduce<Routes<Keys>>((result, page) => {
      return {
        ...result,
        [page.key]: page,
      };
    }, {} as any),
  };
};
