import { useI18next } from "gatsby-plugin-react-i18next";
import { pages, RouteKeys } from "navigation/pages-list";
import * as React from "react";
import { Page } from "../utils/create-pages";

export const useCurrentPage = () => {
  const { originalPath } = useI18next();

  return React.useMemo(() => {
    return pages.find((page): page is Page<RouteKeys> => {
      if (page.exact) {
        return page.path === originalPath;
      }

      return originalPath.startsWith(page.path);
    });
  }, [originalPath]);
};
