import { Navigation } from "@greenpool-eco/components";
import * as React from "react";
import { Actions } from "./actions";
import { navList } from "./nav-list";
import { Link, useI18next } from "gatsby-plugin-react-i18next";

export const Vertical = () => {
  const { t } = useI18next("navigation");
  return (
    <>
      <Navigation.Layout orientation={"vertical"}>
        {navList.map((item) => (
          <Navigation.Item key={item.title}>
            <Link to={item.page.path}>{t(item.title)}</Link>
          </Navigation.Item>
        ))}
      </Navigation.Layout>
      <Actions />
    </>
  );
};
