import { useCurrentPage } from "navigation/use-current-page";
import * as React from "react";
import { Page } from "./create-pages";
import { RouteKeys } from "../navigation/pages-list";

export function usePrevPage(excludes?: Array<RouteKeys>) {
  const page = useCurrentPage();
  const [prevPage, setPrevPage] = React.useState<Page<RouteKeys> | undefined>();

  React.useEffect(() => {
    return () => {
      if (page && !excludes?.includes(page.key)) {
        setPrevPage(page);
      }
    };
  }, [page, excludes]);

  return prevPage;
}
