import { Button } from "@greenpool-eco/components";
import * as React from "react";

export const Actions = () => {
  return (
    <div className="btn-container">
      <a href={createDashboardLink("/login")}>
        <Button>Sign in</Button>
      </a>
      <a href={createDashboardLink("/register")}>
        <Button type={"outline"}>Sign up</Button>
      </a>
    </div>
  );
};

const createDashboardLink = (path: string): string => {
  const url = new URL(path, process.env.DASHBOARD_URL);

  return url.href;
};
